.menu-container{
    width: 20%;
    height: 90vh;
    border-radius: 0 16px 16px 0;
    box-shadow: 0 8px 8px 8px rgba(0, 0, 0, 0.25);
    border: 1px solid #D9D9D9;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.container-title, .container-configuration{
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d9d9d9;
    font-size: 1.5rem;
    color: #7d7d7d;
    border-top: 1px solid#d9d9d9
}
.menu-options{
    height: 80%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}
.menu-option{
    height: 12%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #d9d9d9;
    cursor: pointer;
}
.active{
    background-color: #d9d9d9;
    box-shadow: 14px 25px 11px -20px rgba(0,0,0,0.34) inset;
}